* {
  box-sizing: border-box;
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.main-container {
  display: flex;
  width: 100%;
  background: url(../assets/images/background-page-detail.png);
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center;
}

.container-1440 {
  padding: 158px 0px 0px 0px;
  background: #1f2022;
  width: 100%;
  display: flex;
  justify-content: center;
}
