.header {
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99;
  height: 80px;
  padding: 0 2%;
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1440px;
}

.header-left-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;

  & > * {
    margin-right: 10px;
  }
}

.banner {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  min-height: 100vh;
  margin-bottom: 200px;
  background: url(../../assets/images/banner-frame.png) center no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;
}
.banner-content {
  display: flex;
  position: absolute;
  bottom: -220px;
  flex-direction: column;
  align-items: center;
}

.link {
  color: #809fb8;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 20px;
  padding: 0px 30px;
  font-family: 'Montserrat-Bold';
}

.link-active {
  color: #f4ef85;
  background: #2c2020;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.connect-wallet {
  cursor: pointer;
  height: 50px;
  border: 1px solid #ffd911;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 24px;
  border-radius: 48px;

  span {
    color: white;
    font-weight: 600;
    font-size: 16px;
    font-family: 'Montserrat-SemiBold';
  }
}
.my-account {
  display: flex;
  align-items: center;

  .item {
    min-height: 80px;
    display: flex;
    align-items: center;
    padding: 0 37px;
  }
  .my-acc__address {
    background: #120202;
    flex-direction: column;
    color: white;
    justify-content: center;
  }
  .my-acc__link {
    background: #2C2020;
    font-family: "Montserrat";
    font-weight: bold;
    text-align: center;
    font-size: 20px;
    text-transform: uppercase;
    color: #809FB8;
  }
}
