.detail__hero {
  padding: 140px 24px 0 24px;
  background-size: 100% 100%;
  color: white;
  max-width: 1440px;
  width: 100%;
  padding-bottom: 80px;

  .p-col-12 {
    .p-button {
      width: 120px;
      height: 50px;
      border: 2px solid #6d5373;
      .p-button-label {
        line-height: 1;
        width: 100%;
      }

      &:hover {
        background: rgba(0, 0, 0, 0.2);
      }
    }
  }

  &-content-left {
    .p-panel-toggleable {
      margin-top: 40px;
      .p-panel-header {
        .p-panel-title {
          color: white;
          font-weight: bold;
        }
        background-color: transparent;
      }
      .p-panel-content {
        color: white;
        background-color: transparent;
      }
    }
  }

  &-content-right {
    padding-left: 60px;
    .hero-name {
      text-transform: uppercase;
      font-size: 48px;
      font-family: 'Teko-Bold';
      line-height: 1;
    }
    .owner-by {
      margin-top: 20px;
      // padding-right: 44rem !important;
      .owner {
        margin-right: 16px;
        font-weight: bold;
        font-family: 'Montserrat-Bold';
        line-height: 1;
      }
      .owner-data {
        // font-weight: bold;
        // width: 100px;
        text-overflow: ellipsis;
        // overflow: hidden;
        white-space: nowrap;
        font-family: 'Montserrat-Regular';
        line-height: 1;
      }
    }
    .cross-bar {
      width: 100%;
      margin-top: 20px;
    }
    .filter-slider {
      width: 100%;
      margin-top: 200px;
    }
    .class-faction-level {
      padding: 12px 1% 12px 1%;
      border: 1px solid;
      margin-top: 20px;
      border-radius: 8px;
      border-color: #c4c4c4;
      text-align: center;
      height: 100px;
      .faction-level-layout {
        width: 33.333333333%;
        .faction-layout {
          font-size: 21px;
          font-family: 'Montserrat-SemiBold';
        }
        .faction-data-image {
          margin-top: 12px;
          padding: 5px;
          vertical-align: 8px;
          font-size: 18px;
          font-weight: 200;
          text-align: center;
          font-family: 'Montserrat-Regular';
        }
        .faction-data-yellow {
          color: #ffc525;
          font-size: 18px;
          font-weight: 200;
          margin-top: 12px;
          font-family: 'Montserrat-SemiBold';
        }
        .faction-data-number {
          font-size: 18px;
          font-weight: 200;
        }
      }
    }
    .hero-price {
      align-items: center;
      margin-top: 2rem !important;
      margin-bottom: 2rem !important;
      .value-img {
        width: 30px;
      }
      .value-bnb {
        color: #fe931f;
        font-family: 'Teko-Bold';
        font-size: 48px;
        line-height: 1;
        margin-top: 8px;
      }
      .value-dollar {
        font-size: 24px;
        color: #a39a9a;
        font-family: 'Teko-Light';
        line-height: 1;
      }
    }
    .faction-stat-layout {
      display: flex;

      .p-button-icon-only {
        background-color: transparent;
        border: 0;
      }
    }

    .table-header {
      font-family: 'Teko-Bold';
      margin-bottom: 8px;
    }

    .hero-stat {
      .hero-table {
        width: 20%;
        .hero-value {
          display: flex;
          justify-content: center;
          align-items: center;
          .p-mt-3 {
            padding-bottom: 5%;
          }
          .stat-data {
            font-size: 17px;
            // vertical-align: 10px;
          }
        }
      }
      .hero-skill-table {
        .skill-data-yellow {
          color: yellow;
          font-size: 18px;
          font-weight: 200;
          margin-top: 20px;
        }
        .box-circle-1 {
          position: relative;
          align-items: center;
          .icon-skill {
            width: 60px;
            height: 60px;
            // border-radius: 50px;
          }
          .frame-circle {
            width: 60px;
            height: 60px;
            position: relative;
            margin-top: -60px;
            margin-left: -60px;
          }
          .frame-circle1 {
            width: 30px;
            height: 30px;
            position: relative;
            top: -15px;
            left: -45px;
          }
          .box-circle-2 {
            background: rgb(202, 202, 202);
            border-radius: 50%;
            color: #000000;
            position: absolute;
            background: url(../../assets/images/img_starnumber.png);
            color: #ffffff;
            text-align: center;
            font-weight: bold;
            width: 35px;
            height: 40px;
            right: 23%;
            top: 0;
            padding-top: 7%;
            font-size: 20px;
          }
          .hover-value {
            display: flex;
            justify-content: center;
            align-items: center;
            min-height: 25px;
            .hover-value-scss {
              display: none;
              background-color: #24262e;
              width: 60%;
              border-radius: 5px;
            }
          }
          .box-circle-2:hover + .hover-value .hover-value-scss {
            display: block;
          }

          .box-circle-3 {
            position: absolute;
            background: url(../../assets/images/img_starnumber.png);
            color: #ffffff;
            text-align: center;
            font-weight: bold;
            width: 35px;
            height: 40px;
            right: 10%;
            top: 0;
            padding-top: 5%;
            font-size: 20px;
          }
        }
      }
    }
    .hero-offer {
      .p-datatable {
        margin-top: 40px;
        .p-datatable-tbody {
          tr {
            td {
              border: 0;
            }
          }
        }
        .p-column-title {
          color: #ffd911;
          font-family: 'Montserrat-SemiBold';
        }
      }
    }

    .group-btn {
      margin-top: 40px;
      .p-button-label {
        font-size: 20px;
        font-weight: bold;
      }
      .btn-buy {
        width: 40%;
        height: 60px;
        background-color: hsl(50, 100%, 35%);
        border-radius: 30px;
        border-color: #ffd911;
      }
      .btn-offer {
        width: 40%;
        height: 60px;
        background-color: transparent;
        border-radius: 30px;
        border-color: #ffd911;
      }
    }
  }

  &-sales-history {
    h2 {
      margin-left: 10px;
      margin-top: 40px;
    }

    // .p-datatable {
    .p-datatable-striped {
      .p-datatable-body {
        // .p-row-odd {
        tr.p-row-odd {
          background-color: #000000;
        }
        // }
      }
    }
    // }
  }
  &-more-collection {
    margin-top: 40px;
  }
  &-button-back {
    height: 60px;
    width: 8%;
    font-weight: bold;
    font-size: 20px;
    background-color: transparent;
    border-color: #6d5373;
  }
  .p-datatable.p-datatable-striped {
    margin-top: 40px;
    th {
      background-color: #24262e;
    }
    tr.p-row-odd {
      td {
        color: white;
        background: #24262e;
      }
    }
    tr {
      td {
        color: white;
        background: #1d181f;
      }
    }
    .p-column-title {
      color: #ffd911;
    }
  }
}

.btn-accept {
  width: 55%;
  height: 25px;
  background-color: transparent;
  border-radius: 25px;
  border-color: #ffd911;
  font-size: 1rem;
  font-weight: 600;
}

.DataTable-priceBND {
  font-weight: 600;
}

.DataTable-priceUSD {
  color: #8a8a8a;
}

.hero-offer-table {
  > div {
    > table {
      > thead {
        > tr {
          .DataTable-timeheader {
            padding-left: 50px;
          }
          .p-sortable-disabled {
            border: 0;
          }
        }
      }
      > tbody {
        > tr {
          td {
            border: 0;
          }
        }
      }
    }
  }
}

.hero-sales-history-table {
  > div {
    > table {
      > thead {
        > tr {
          .DataTable-header {
            padding-left: 50px;
          }
          .p-sortable-disabled {
            border: 0;
          }
        }
      }
      > tbody {
        > tr {
          .DataTable-sales-history {
            border: 0;
            font-weight: 500;
          }
        }
      }
    }
  }
}

.p-datatable {
  table {
    margin-left: auto;
    margin-right: auto;
    width: 98%;
  }
}

.detail-image {
  height: 30px;
  width: 22px;
}
.detail-span {
  font-size: 26px;
}
.detail-address {
  color: #00c2ff;
}
.p-button {
  background-color: transparent;
  border: 0;
  height: 30px;
  .icon-info {
    width: 24px;
  }
}
.p-dialog {
  width: 30vw !important;
  border-radius: 10px;
  background-color: #24262e;
  .p-dialog-header {
    color: #ffffff;
  }
  .p-dialog-content {
    color: #ffffff;
    .stat-info-table {
      .stat-info-data {
        display: flex;
        flex-wrap: wrap;
        .stat-info-col {
          flex: 50%;
        }
      }
      .stat-info-level {
        .level-note {
          font-size: 12px;
          font-weight: 200;
        }
      }
    }
  }
}
