}

.floatlabel-demo {
  .p-field {
    margin-top: 2rem;
    margin-bottom: 0;
  }
}

.layout-topbar {
  .notifications {
    .layout-topbar-action-panel {
      img {
        width: 32px;
        height: 32px;
      }

      .layout-topbar-action-item {
        border-bottom: 1px solid var(--divider-color);

        &:last-child {
          border-bottom: 0 none;
        }
      }
    }
  }

  .app {
    .layout-topbar-action-panel {
      button {
        padding: 0.5rem 0;
        margin: auto;

        i {
          width: 42px;
          height: 42px;
          border: 1px solid transparent;
          border-radius: 50%;
          margin: 0.5rem 0;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
    }
  }
}

.layout-rightmenu {
  .header {
    padding-bottom: 0.5rem;
    margin-bottom: 0.714rem;
    border-bottom: 1px solid var(--divider-color);
  }

  .online-members {
    img {
