.container-item {
  position: relative;
  // padding: 20px 15px;
  border: 1px solid var(--surface-d);
  margin: 0 0 100px 0;

  .content-up {
    position: relative;

    .frame-item {
      position: relative;
      width: 100%;
      z-index: 2;
    }
    .background-item {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      right: 0;
      z-index: 3;
    }
    .background-data {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      z-index: 4;
      .data-star {
        width: 40px;
        height: 40px;
        margin-right: -16px;
      }
      .data-point {
        color: #c6c6c6;
        font-size: 20px;
        padding-left: 4%;
      }

      .data-point {
        font-family: 'Teko-Bold';
        font-size: 24px;
        color: #fff;
      }
    }
    .footer-item {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 150;
      z-index: 2;
    }
    .element-canvas {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      z-index: 3;
    }
    .item-data {
      position: absolute;
      right: 4px;
      top: 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      z-index: 5;

      .item-element {
        width: 24px;
        margin-top: 24px;
      }

      .item-gender {
        text-transform: uppercase;
        font-weight: bold;
        color: white;
        margin-top: 5px;
        font-size: 12px;
      }
    }
  }
  .content-down {
    background-color: #201b21;

    .item-footer {
      text-align: center;
      display: flex;
      align-items: flex-start;
      justify-content: center;
      padding-top: 20px;

      &-token {
        margin-right: 7px;
      }

      &-price {
        font-size: 30px;
        text-align: left;
        font-weight: bold;
        color: white;
      }
      &-value {
        font-size: 14px;
        text-align: center;
        font-weight: normal;
      }
    }

    .frame-buy {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 20px;
    }
  }
}
