.market {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 0 2%;
  background-size: 100%;
  padding-top: 120px;
  max-width: 1440px;
  width: 100%;

  &__group-switch {
    color: white;
    text-align: center;
    font-weight: bold;
    font-family: 'Montserrat-SemiBold';

    .p-inputswitch {
      margin-top: 12px;
      &.p-inputswitch-checked {
        .p-inputswitch-slider {
          &:hover {
            background-color: #3a36db;
          }
          background-color: #3a36db;
        }
      }
      .p-inputswitch-slider {
        &:hover {
          background-color: #2b3441;
        }
        background-color: #2b3441;
      }
    }
  }

  &__filter {
    // width: 460px;
    // border-radius: 8px;
    // padding: 30px 20px 10px;
    // margin: 69px 0 20px;

    &-container {
      position: sticky;
      top: -50px;
      margin-bottom: 40px;
    }
  }
  &__content {
    width: calc(100% - 400px);

    .market-search {
      margin-bottom: 20px;
      margin-left: -10px;
      .filter-header {
        width: 30%;
        .input-number-id {
          background-color: transparent;
          color: white;
          width: 48%;
        }
        .custom-number-heroid::-webkit-inner-spin-button, .custom-number-heroid::-webkit-outer-spin-button {
          -webkit-appearance: none;
          margin: 0;
        }
        .custom-number-heroid[type=number]{
          -moz-appearance: textfield;
        }
        .p-dropdown {
          background-color: transparent;
          width: 48%;
          height: 40px;
          align-items: center;
          .p-inputtext {
            color: white;
            margin-left: 12px;
          }
          .p-placeholder {
            color: #baafbc;
          }
        }
      }
      .p-search {
        // display: flex;
        // justify-content: flex-end;
        margin-left: 4%;
        .p-input-icon-left {
          width: 150%;
          .p-inputtext {
            height: 40px;
            width: 100%;
            background-color: transparent;
            color: white;
            border-radius: 100px;
          }
        }

        .amount-total {
          flex: 1;
          text-align: right;
          color: white;
          font-weight: 600;
          font-size: 20px;
          margin-bottom: 0;
          line-height: 40px;
          font-family: 'Montserrat-SemiBold';
        }
      }
    }
  }
}

.data-view {
  min-height: 1000px;
  .p-dataview-content {
    background-color: transparent;
    .container-item {
      position: relative;
      border: 2px solid var(--surface-d);
      margin: 0 0 26px 26px;
      border-radius: 30px;
      border-color: #3d3f40;

      .content-up {
        position: relative;
        height: 300px;

        .background-item {
          position: absolute;
          width: 100%;
          height: 100%;
          z-index: 1;
          // border-radius: 30px;
          border-top-left-radius: 30px;
          border-top-right-radius: 27px;
        }
        .footer-item {
          position: absolute;
          bottom: 0;
          right: 0;
          width: 100%;
          z-index: 2;
        }
        .container-hero-star {
          position: absolute;
          top: 0;
          z-index: 2;
          background-color: rgba(0, 0, 0, 0.7);
          padding: 0px 24px 0px 12px;
          border-top-left-radius: 30px;
          clip-path: polygon(0 0, 100% 0%, 90% 100%, 0% 100%);
          .image-star {
            width: 25px;
            height: 25px;
            margin-right: -10px;
          }
          .image-star-red {
            width: 25px;
            height: 25px;
          }
        }
        .element-canvas {
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          width: 100%;
          height: 100%;
          z-index: 3;
        }
        .item-data {
          position: absolute;
          right: 4%;
          top: 0;
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;
          z-index: 4;

          .item-element {
            width: 24px;
            margin-top: 24px;
          }

          .item-gender {
            text-transform: uppercase;
            font-weight: bold;
            color: white;
            margin-top: 5px;
            font-size: 12px;
          }
        }
      }

      .content-down {
        background-color: #201b21;
        padding: 8px 16px 16px 16px;
        border-bottom-left-radius: 26px;
        border-bottom-right-radius: 30px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        .content-down-left {
          display: flex;
          flex-direction: column;

          .id {
            color: #ffda57;
            font-size: 16px;
            font-family: 'Teko-Regular';
          }

          .name {
            color: #9a9a9a;
            font-size: 22px;
            font-family: 'Teko-Bold';
            line-height: 1;
          }

          .point {
            color: #939393;
            font-size: 16px;
            font-family: 'Teko-Regular';
          }
        }

        .content-down-right {
          display: flex;
          flex-direction: column;
          align-items: flex-end;

          .price {
            display: flex;
            align-items: center;

            img {
              width: 24px;
              height: 24px;
              margin-right: 8px;
            }

            .hero-token {
              color: #fff;
              font-family: 'Teko-Bold';
              font-size: 24px;
              line-height: 1;
            }
          }

          .hero-money {
            color: #838383;
            font-family: 'Teko-Light';
            font-size: 24px;
          }
        }
      }
    }
  }
  .p-paginator-bottom {
    background-color: transparent;
    border-width: 0;

    .p-paginator-first,
    .p-paginator-prev,
    .p-paginator-next,
    .p-paginator-last {
      border: 1px solid #fffd4f;
      background-color: none;
      color: #fff !important;
      opacity: 1 !important;

      &:hover {
        background-color: rgb(82, 82, 82) !important;
        border: 1px solid #fffd4f !important;
      }
    }

    .p-paginator-pages {
      margin: 0px 8px;

      button {
        color: white;
        font-size: 18px;
        font-family: 'Teko-Bold';
        border: 1px solid rgb(124, 124, 124);
      }

      .p-highlight {
        background: none !important;
        color: white;
        border: 1px solid #fffd4f;
      }
    }
  }

  margin-bottom: 40px;
}

.filter-item {
  width: 400px;

  .filter-content {
    border: 2px solid;
    padding: 10px 20px;
    border-color: #647979;
    border-radius: 10px;

    .filter-header {
      font-family: 'Montserrat-Bold';

      .p-button-label,
      div {
        color: white;
        text-transform: uppercase;
        font-size: 18px;
        font-weight: bold;
      }
      .p-button {
        padding: 0;
      }
    }

    .cross-bar {
      margin: 12px 0 12px 0;
      width: 100%;
    }

    .form {
      h3 {
        color: #fe931f;
        margin-top: 20px;
        font-family: 'Teko-Bold';
      }
      h5 {
        color: white;
      }
      .p-slider {
        margin-bottom: 12px;
        .p-slider-handle{
          width: 25px;
          height: 25px;
          margin-top: -12px;
        }
        .p-slider-range {
          background-color: #6d5373;
        }
      }
      .value-switch {
        color: white;
        font-weight: bold;
        font-size: 18px;
        margin-bottom: 8px;
        font-family: 'Teko-Bold';
      }
      .filter-content-item {
        display: flex;
        flex-wrap: wrap;
        .p-field-checkbox {
          width: 50%;

          img {
            margin-left: 16px;
            margin-right: 8px;
          }

          div {
            background: transparent;
          }
          label {
            color: white;
            font-family: 'Montserrat-Regular';
          }
        }
      }
      .body-part-container.p-d-flex {
        margin-top: 20px;
        .body-part-switch {
          flex: 2;
        }
      }
      .label-item-body-part {
        color: white;
        flex: 1;
        font-weight: bold;
        font-size: 20px;
        font-family: 'Teko-Bold';
      }
    }
  }
}
.testspinner {
  display: flex;
  justify-content: center;
  .pi-spinner {
    padding: 50%;
    font-size: 5em;
    color: white;
  }
}
