.special__summon {
  background: url(../../assets/images/bg_special_summon.png) no-repeat center
    center;
  min-height: 100vh;
  width: 100%;
  font-family: 'Montserrat-SemiBold';
  background-size: 100%;

  h1 {
    margin-top: 10%;
    color: white;
  }
  .p-button-rounded {
    background-color: #2156e1;
    color: white;
    border-radius: 20px;
    width: 241px;
    height: 40px;
    border-color: transparent;
    font-family: 'Montserrat-SemiBold';
    margin-top: 40px;
  }
  &-form {
    margin-top: 20px;
    width: 50%;
    position: relative;
    margin-top: 16px;

    label {
      margin-bottom: 20px;
      color: white;
      font-size: 16px;
      margin-bottom: 12px;
    }
    span {
      button {
        background-color: transparent;
        border: 0;
        border-right: solid #faff00 1px;
        border-top: solid #faff00 1px;
        border-bottom: solid #faff00 1px;
      }
      .p-inputnumber-button-group {
        height: 50px;
      }
      input {
        border-radius: 10px;
        background-color: transparent;
        border-color: #faff00;
        color: #ffffff;
        font-size: 16px;
        padding-left: 16px;
      }
    }
    .p-inputnumber {
      height: 50px;
      border-color: #faff00 !important;
    }

    .bnb-token {
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      bottom: 10px;
      right: 16px;

      span {
        color: white;
        font-size: 16px;
        margin-left: 8px;
      }
    }
  }
}
.p-dialog-mask {
  background: url(../../assets/images/bg_special_summon.png) no-repeat center
  center;
  min-height: 100vh;
  width: 100%;
  font-family: 'Montserrat-SemiBold';
  background-size: 100%;
  .p-dialog {
    min-width: 700px;
    border-radius: 20px;
    .p-dialog-header {
      background-color: transparent;
      text-align: center;
      margin-right: -60px;
      margin-top: 15px;
      .p-dialog-title {
        font-size: 36px !important;
      }
      .p-dialog-header-icons {
        display: block;
        margin-right: 40px;
        margin-top: -30px;
      }
    }
    .p-dialog-content {
      background-color: transparent;
      margin-top: 20px;
      text-align: center;
    }
    .dialog-button {
      text-align: center;
      display: flex;
      justify-content: space-evenly;
      .p-button {
        color: white ;
        margin-top: 50px;
        text-align: center;
        margin-bottom: 20px;
        width: 300px;
        height: 50px;
        border-radius: 20px;
        font-size: 16px;
        background-color: #2156E1;
        font-family: 'Montserrat-SemiBold'
      }
      .p-button-text {
        background-color: transparent;
        border: #30F3FF 1px solid;
      }
    }

    // .dialog-data {
    //   text-align: center;
    // }
  }
  .dialog-list {
    background-color: transparent;
    min-width: 800px;
    // min-height: 500px;
    border: none;
    border-radius: 0;
    .p-dialog-header {
      margin-top: -10px;
      margin-right: 0;
      .p-dialog-title {
        h1 {
          color: white;
          text-align: center;
          font-size: 36px;
        }
        h2 {
          color: white;
          text-align: center;
          font-size: 16px;
          font-weight: 200;
        }
      }
      .p-dialog-header-icons {
        display: none; //chuyen thanh none
      }
    }
    .modal-content {
      align-items: center;
      justify-content: center;
    }
    .p-dialog-content {
      min-height: 440px;
      background-color: transparent;
      align-items: center;
      justify-content: center;
      margin-top: -20px;
      display: flex;
    }
    .layoutmodal_body_div {
      background: url(../../assets/images/banner-specialsummon.png) center/839px 534px  no-repeat;
      color: white;
      text-align: center;
      background-position: center;
      background-repeat: no-repeat;
      width: 585px;
      height: 366px;
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      padding-top: 0;

      // padding: 140px 0px;
      .layoutmodal_dialog {
        .dialog-tag-1 {
          align-items: center;
          display: flex;
          justify-content: space-evenly;
          .dialog-number {
            font-size: 15px;
          }
          .circle_summon {
            width: 5%;
          }
          .star_summon {
            width: 8%;
          }
          .dialog-data {
            font-size: 15px;
            width: 20%;
          }
          .dialog-faction {
            align-items: center;
            display: flex;
            justify-content: space-evenly;
            width: 10%;
            .faction-name {
              font-size: 10px;
            }
          }
        }
        .dialog-tag-2 {
          margin-top: -20px;
          align-items: center;
          display: flex;
          justify-content: space-evenly;
          .dialog-number {
            font-size: 15px;
          }
          .circle_summon {
            width: 5%;
          }
          .star_summon {
            width: 8%;
          }
          .dialog-data {
            font-size: 15px;
            width: 20%;
          }
          .dialog-faction {
            align-items: center;
            display: flex;
            justify-content: space-evenly;
            width: 10%;
            .faction-name {
              font-size: 10px;
            }
          }
        }
        .iconpass {
          width: 75%;
          height: 0.5%;
        }
      }
    }
  }
}


