.p-dialog {
  .p-dialog-header, .p-dialog-content {
    background: #1F2022;
  }
  .p-dialog-header {
    border-radius: 10px 10px 0 0;
  }
  .p-dialog-content {
    border-radius: 0 0 10px 10px;
  }
}
.register {
  border-radius: 50px;
  min-width: 600px;
  .p-inputtext {
    background-color: #1F2022 !important;
    border: 1px solid #4D505B;
    box-sizing: border-box;
    border-radius: 25px;
    color: white;
    width: 100%;
  }
  &__title {
    font-family: 'Teko-Medium';
    font-style: normal;
    font-weight: 600;
    font-size: 36px;
    text-align: center;
    color: #FFFFFF;
  }
  &__footer {
    max-width: 600px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    text-align: center;
    color: #FFFFFF;
    a {
      color: #00F0FF;
    }
  }
  &__install {
    text-align: center;
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 15px;
    text-align: center;
    color: #FFFFFF;
    .install {
      &__icon {
        margin: 0 auto 38px auto;
        width: 137px;
        height: 108px;
        img {
          width: 100%;
        }
      }
      &__back {
        text-align: left;
        i {
          cursor: pointer;
          padding: 10px;
        }
      }
      &__link {
        color: #00F0FF;
        margin: 8px 0 36px 0;
        cursor: pointer;
      }
      &__btn {
        background: #2156E1;
        padding: 10px 20px;
        border-radius: 20px;
        max-width: 100px;
        margin: 0 auto;
        cursor: pointer;
      }
    }
  }
  &__wallets {
    display: flex;
    justify-content: center;
  }
  .form-register {
    color: white;
    width: 80%;
    margin: 0 auto;
    .register {
      &__input {
        margin-bottom: 24px;
        .p-input-icon-right {
          width: 100%;
        }
      }
      &__checkbox {
        display: flex;
        align-items: flex-start;
        a {
          color: #00F0FF;
        }
        .p-checkbox-box {
          width: 15px;
          height: 15px;
        }
      }
      &__btn {
        text-align: center;
        .p-button {
          background: #2156E1;
          border-radius: 20px;
          padding: 0.5rem 2rem;
        }
      }
      &__noti {
        text-align: center;
        margin: 16px 0 46px 0;
      }
    }
    .input__label {
      color: white;
      margin-bottom: 16px;
    }
  }
  .wallet {
    &__item {
      width: 212px;
      height: 168px;
      margin: 47px 27px;
      cursor: pointer;
      img {
        width: 100%;
      }
    }
  }
  .error {
    color: white;
    &__title {
      font-size: 20px;
      padding-bottom: 20px;
    }
    &__content {
      padding-bottom: 20px;
    }
  }
}